import React, { useState } from "react";
import InfiniteScroll from "react-infinite-scroll-component";
import Grid from '@material-ui/core/Grid';
import Event from '../components/eventCard';

const style = {
  height: 30,
  border: "1px solid green",
  margin: 6,
  padding: 8
};
function Scroll(){
  
  const [items, setItem] = useState(Array.from({ length: 28 }));
  const [hasMore, setHasMore] = useState(true);

  const fetchMoreData = () => {
    if (items.length >= 200) {
      setHasMore(false);
      return;
    }
    // a fake async api call like which sends
    // 20 more records in .5 secs
    setTimeout(() => {
      setItem(items.concat(Array.from({ length: 28 })));
    }, 1000);
  };

    return (
        <InfiniteScroll
          dataLength={items.length}
          next={fetchMoreData}
          hasMore={hasMore}
          loader={<h4>Loading...</h4>}
          endMessage={
              <p style={{ textAlign: "center" }}>
              <b>Yay! You have seen it all</b>
            </p>
          }
          >
              <Grid container >
        
          {items.map((i, index) => (
              <>
                <Grid item alignContent={'space-between'}>
                    <Event title={'Recolha Lixo'} date={'September 14, 2021'} image={'../logo.svg'} description={'Descrição teste'} />
                    <br />
                </Grid>    
              </>
          ))}
          </Grid>
        </InfiniteScroll>
    );
}

export default Scroll;
 /*<div style={style} key={index}>
              div - #{index}
            </div>*/