import React from 'react';
import { useHistory, Link, Redirect } from 'react-router-dom';
import frontend from '../actions/givers';
import NavBar from '../navbar';

function Profile() {
  if(!frontend.Authenticated()){
    return <Redirect to="/"/>
  }
   
  return (
    <>  
      <NavBar/>
    </>
  );
}

export default Profile;