import React from 'react';
import NavBar from '../navbar';
import TextField from '@material-ui/core/TextField';

function Profile() {

  return (
    <>  
      <NavBar/>
      <br/>
      {"Email"}
      <TextField
            variant="outlined"
            margin="normal"
            fullWidth
            name="password"
            label="teste@gmail.com"
            type="email"
            id="password"
            autoComplete="current-password"
          />
    </>
  );
}

export default Profile;